/* eslint-disable react/jsx-no-useless-fragment */
import type { SerializedStyles } from "@emotion/react";
import { css, jsx, useTheme } from "@emotion/react";
import { Flex, width100, padding as _padding } from "@toss/emotion-utils";
import type {
  InputHTMLAttributes,
  ReactNode,
  TextareaHTMLAttributes
} from "react";
import { useEffect, useRef } from "react";

type InputAndTextarea = InputHTMLAttributes<HTMLInputElement> &
  TextareaHTMLAttributes<HTMLTextAreaElement>;

export interface Props extends InputAndTextarea {
  /**
   *   HTML .
   *
   * @default 'input'
   */
  as?: "input" | "textarea";

  /**
   * input value .
   */

  value?: string;

  /**
   * input   .     .
   *
   * preAppend  input `margin-left: 14px`  .
   */
  preAppend?: ReactNode;

  /**
   * input        . (: px)
   */
  height?: number;

  /**
   * padding     . (SerializedStyles)
   *
   * @default 18
   */
  padding?: SerializedStyles;

  /**
   * input   boolean   .
   */
  autoFocus?: boolean;

  /**
   * input blur    .
   */
  onBlur?: VoidFunction;
  /**
   *    .
   */
  absoluteNode?: ReactNode;
}

const Input = ({
  as = "input",
  preAppend,
  padding = _padding({ x: 18 }),
  autoFocus = false,
  height,
  absoluteNode,
  ...rest
}: Props) => {
  const { colors } = useTheme();
  const inputRef = useRef<HTMLInputElement | null>(null);
  // input auto focus    
  useEffect(() => {
    if (!autoFocus) return;
    const valueLength = inputRef.current?.value.length ?? 0;
    inputRef.current?.focus();
    inputRef.current?.setSelectionRange(valueLength, valueLength);
  }, [autoFocus]);

  return (
    <Flex
      css={css`
        position: relative;
        ${width100}
      `}
    >
      <>
        {preAppend && <div>{preAppend}</div>}
        {jsx(
          as,
          {
            // textarea   156 , fixedHeight    
            css: css`
              ${width100}
              ${padding}
              height:${height ? `${height}px` : "unset"};
              margin-left: ${preAppend ? "14px" : "auto"};
              border: 1px solid ${colors.secondary.B8};
              border-radius: 10px;
              font-family: inherit;

              &:disabled {
                background-color: ${colors.white};
              }
            `,
            ref: inputRef,
            ...rest
          },
          null
        )}
        {absoluteNode && <>{absoluteNode}</>}
      </>
    </Flex>
  );
};
export default Input;
