import type { ChangeEvent } from "react";
import { useCallback, useState } from "react";
import { useDebounce as useDebounceHook } from ".";

interface Props {
  /**
   * input state initializing    .
   *
   * @default ''
   */
  initialValue?: string;

  /**
   * debounce    .
   *
   * @default `false`
   */
  useDebounce?: boolean;

  /**
   * debounce    timeout . (: ms)
   *
   * @default `150`
   */
  debounceTimeout?: number;
}

/**
 * @name useInput
 * @description
 * input   string      .
 *
 * @example
 * const { value, setValue, onChange } = useInput({ useDebounce: false });
 * const { value, debouncedValue, setValue, onChange } = useInput({ useDebounce: true });
 * const { value, debouncedValue, onChange } = useInput({
 *   useDebounce: true,
 *   debounceTimeout: 300
 * });
 */
const useInput = ({
  initialValue = "",
  useDebounce = false,
  debounceTimeout = 150
}: Props) => {
  const [value, setValue] = useState<string>(initialValue);
  const [debouncedValue, setDebouncedValue] = useState<string>(initialValue);

  const handleSetDebounceValue = useDebounceHook(
    (value: string) => setDebouncedValue(value),
    debounceTimeout
  );

  const handleSetValue = useCallback(
    (value: string) => {
      setValue(value);
      if (useDebounce) {
        handleSetDebounceValue(value);
      }
    },
    [handleSetDebounceValue, useDebounce]
  );

  const onChange = useCallback(
    (
      event: ChangeEvent<HTMLInputElement> | ChangeEvent<HTMLTextAreaElement>
    ) => {
      handleSetValue(event.target.value);
    },
    [handleSetValue]
  );

  return {
    props: {
      value,
      /**
       * input  onChange   .
       *
       * ```js
       * <input value={...} onChange={onChange} />
       * ```
       */
      onChange
    },

    setValue: handleSetValue,

    /**
     * value debounce string.
     *
     * `useDebounce` `true`   .
     */
    debouncedValue
  };
};
export default useInput;
