import { useCallback, useEffect, useRef } from "react";

/**
 * @name usePreservedCallback
 * @description
 *  mount      `callback`   .
 *
 *  `callback` React `Ref`    .
 *
 * ```typescript
 * const callback = usePreservedCallback<Callback>(
 *   //    (: Callback)
 *   callback
 * )
 * ```
 *
 * @example
 * const callback = usePreservedCallback(() => {
 *   alert('    ');
 * });
 *
 * useEffect(() => {
 *   //  Effect 1 .
 *   callback();
 * }, [callback])
 * @ref
 * https://slash.page/libraries/react/react/src/hooks/usepreservedcallback.ts.tossdocs/
 */
const usePreservedCallback = <Callback extends (...args: any[]) => any>(
  callback: Callback
) => {
  const callbackRef = useRef<Callback>(callback);

  useEffect(() => {
    callbackRef.current = callback;
  }, [callback]);

  return useCallback(
    (...args: any[]) => {
      return callbackRef.current(...args);
    },
    [callbackRef]
  ) as Callback;
};

export default usePreservedCallback;
